* {
  position: relative;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

body {
  background-size: cover;
  background: no-repeat center #eb9f12bd;
  color: #fff;
}

.simulator {
  width: 120vmin;
  height: 90vmin;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.03);
}

.output, .report{
  position: fixed;
  font-size: 1rem;
  background: rgba(0, 0, 0, 0.6);
  padding: 0.7rem 0.8rem;
  color: #ffffff;
  width: 40vmin;
  height: 90vmin;
  text-align: left;
  line-height: 35px;
  z-index: 999;
}

.report {
  top: 0%;
  left: 0%;
}

.output {
  top: 0%;
  right: 0%;
  max-width: 90vmin;
}

.square {
  display: flex;
  background: #fff;
  border: 1px solid #414c69;
  float: left;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
}

.nextline{
  clear: both;
  content: ""
}

.button {
  position: fixed;
  bottom: 0%;
  width: 118vmin;
  height: 10vmin;
}

.mleft-30{
  margin-left: 30px;
}

.mtop-20{
  margin-top: 20px;
}

.mtop-40{
  margin-top: 40px;
}

.input-box{
  position: fixed;
  bottom: 0%;
  left: 10%;
  z-index: 9000;
  width: 28vmin;
}

.select-wrapper input.select-dropdown {
  border-bottom: 2px solid darkcyan;
  font-size: 16px;
  color: darkgray
}

.btn {
  font-size: 16px;
}

.mode{
  color: black;
}

.side-menu{
  position: fixed;
  bottom: 3%;
  left: 1%;
  z-index: 9000;
}

.sidenav{
  font-size: 1rem;
  background-color: rgba(0, 0, 0, 0.9);
  text-align: left;
  width: 400px;
  line-height: 60px;
}

.select-wrapper .caret{
  fill: white;
  height: 28px;
  width: 28px;
}

.btn:disabled, .btn[disabled]
{
  background-color: gray !important;
  color: #fff !important;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px
             rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2) !important;
}

.btn-floating:disabled,.btn-floating[disabled]
{
  background-color: gray !important;
}

.material-tooltip {
  background-color: rgba(235, 159, 18, 0.2);
  color: black
}

.content{
  margin-left: 5px;
  margin-top: 10px;
  height: 75vmin;
  overflow-y: scroll;
}

.radius{
  border-radius: 10px;
}

.card-panel {
  padding: 12px;
  margin: 0px
}

td,th {
   padding: 2px;
   text-align: center;
}

p{
  word-wrap: break-word !important;
}

[type="radio"]:not(:checked) + span, [type="radio"]:checked + span{
  font-size: 14px;
}

.card{
  margin: 0px;
  opacity: 0.8;
}

.bold{
  font-weight: 400 !important;
}

.m{
  width: 70vmin;
  max-height: 30vmin;
}

h5{
line-height: 10%;
margin: 16px;
}

.action-go{
  position: fixed;
  bottom: 9%;
  right: 1%;
  z-index: 9000;
}

.action{
  width: 28vmin;
}

.mtop-5{
  margin-top: 5px;
}

.middle{
  position: fixed;
  bottom: 50%;
  left: 37%;
  width: 50vmin;
  max-height: 30vmin;
  z-index: 9000;
}
